import { Link, matchRoutes, useLocation } from "react-router-dom";
import { Nav } from "./Nav";
import { useIsMobile } from "hooks/use-is-mobile";
import { useOpenState } from "@horat1us/react-hooks";
import { MenuHamburger } from "./MenuHamburger";
import { ResponsiveNav } from "./ResponsiveNav";
import styles from "./styles.module.scss";
import logo from "./logo.svg";
import logoColored from "./logo-colored.svg";
import logoCutted from "./logo-cutted.svg";
import classNames from "classnames";
import { useWindowVerticalPosition } from "hooks/use-window-vertical-position";
import { ClientLink } from "components/client-link";

export const Layout = () => {
    const isMobile = useIsMobile();
    const [isOpen, handleOpenChange] = useOpenState(false);
    const scrollPosition = useWindowVerticalPosition();
    const { pathname } = useLocation();
    const postPaths = [{ path: "/blog/:slug" }, { path: "/getcash/:slug" }, { path: "/account-deletion" }];
    const isPostPath = matchRoutes(postPaths, pathname);
    const isPageScrolled: boolean = scrollPosition > 1; // 1 instead of 0 - clearance for touch-devices;
    const isHeaderFixed = isPageScrolled || isPostPath;

    return (
        <>
            <header
                className={classNames(
                    styles.header,
                    isHeaderFixed ? styles.headerIsFixed : ""
                )}
            >
                <div
                    className={styles.container}
                    style={{ alignItems: "center" }}
                >
                    <Link to="/" className={styles.link}>
                        <img
                            src={
                                isHeaderFixed
                                    ? isMobile
                                        ? logoCutted
                                        : logoColored
                                    : isMobile
                                    ? logoColored
                                    : logo
                            }
                            alt="Logo"
                            width={isHeaderFixed ? "96" : "158"}
                            height="auto"
                            className={styles.logo}
                        />
                    </Link>
                    {isMobile ? (
                        <>
                            {isHeaderFixed && <ClientLink />}
                            <MenuHamburger
                                isOpen={isOpen}
                                onClick={handleOpenChange}
                            />
                        </>
                    ) : (
                        <div className="flex items-center">
                            <Nav />
                            {isHeaderFixed && <ClientLink />}
                        </div>
                    )}
                </div>
            </header>
            {isMobile && (
                <ResponsiveNav
                    onClick={handleOpenChange}
                    className={isOpen ? "responsive_active" : ""}
                >
                    <Nav onClick={handleOpenChange} />
                </ResponsiveNav>
            )}
        </>
    );
};
