import React, { ReactElement } from "react";
import * as Configuration from "modules/api/use-config";
import * as Icon from "./icons";
import { formatPhoneNumber } from "utils/format-phone-number";
import "./contacts.scss";
import { FacebookColored } from "components/social/FacebookColored";

type ContactsType = {
    icon: React.ReactNode;
    title: string;
    element: ReactElement | Array<ReactElement>;
};

type StylesType = "contacts_unstyled" | "contact-list";

export const Contacts: React.FC<{
    className?: StylesType;
    withSocial?: boolean;
}> = ({ className, withSocial }) => {
    const config = Configuration.useConfig();

    const contactsData: Array<ContactsType> = [
        {
            icon:
                className === "contacts_unstyled" ? (
                    <Icon.EmailMono />
                ) : (
                    <Icon.Email />
                ),
            title: "Email",
            element: <a href="mailto:info@kobogo.ng">info@kobogo.ng</a>,
        },
    ];

    if (config?.phones) {
        contactsData.unshift({
            icon:
                className === "contacts_unstyled" ? (
                    <Icon.PhoneMono />
                ) : (
                    <Icon.Phone />
                ),
            title: "Phone",
            element: config.phones.support.map((item) => (
                <a key={item} href={`tel:+${item}`}>
                    {formatPhoneNumber(item)}
                </a>
            )),
        });
    }

    if (withSocial) {
        contactsData.push({
            icon: <FacebookColored />,
            title: "Facebook",
            element: (
                <a
                    href="https://www.facebook.com/people/KoboGo/100094588816746/"
                    target="_blank"
                    rel="noreferrer"
                >
                    @KoboGo
                </a>
            ),
        });
    }

    return (
        <ul className={className ?? "contacts"}>
            {contactsData.map(({ icon, title, element }) => (
                <li key={title}>
                    {icon}
                    <div className="mt-[2px] pl-3 w-full">
                        <h4 className="leading-none">{title}</h4>
                        <div>{element}</div>
                    </div>
                </li>
            ))}
        </ul>
    );
};
