import React from "react";
import { useIsMobile } from "hooks/use-is-mobile";
import { NavLink } from "react-router-dom";
import styles from "./styles.module.scss";
import { Dropdown } from "./Dropdown";

type Link = {
    title: string;
    link: string;
};

export type NavItem = {
    title: string;
    link?: string;
    children?: Array<Link>;
};

const navList: Array<NavItem> = [
    {
        title: "HOME",
        link: "",
    },
    {
        title: "ABOUT",
        link: "/about",
    },
    {
        title: "HOW TO",
        children: [
            {
                title: "Get first loan",
                link: "/how-to/get-first-loan",
            },
            {
                title: "Get next loans",
                link: "/how-to/get-next-loan",
            },
            {
                title: "Repay the loans",
                link: "/how-to/repay-loan",
            },
            {
                title: "Extend a loans",
                link: "/how-to/extend-loan",
            },
        ],
    },
    {
        title: "FAQ",
        link: "/faq",
    },
    {
        title: "NEWS",
        link: "/blog",
    },
    {
        title: "CONTACTS",
        link: "/contacts",
    },
];

export const Nav: React.FC<{ onClick?: () => void }> = ({ onClick }) => {
    const isMobile = useIsMobile();
    const responsiveNavClose = isMobile ? onClick : undefined;

    return (
        <nav className={styles.nav}>
            {navList.map((item) => (
                <li key={item.title} className={styles.item}>
                    {item.children ? (
                        <Dropdown item={item} navClose={responsiveNavClose} />
                    ) : (
                        <NavLink
                            key={item.title}
                            to={`${item.link}`}
                            className={({ isActive }) =>
                                isActive ? styles.activeNavLink : ""
                            }
                            onClick={responsiveNavClose}
                            end
                        >
                            {item.title}
                        </NavLink>
                    )}
                </li>
            ))}
        </nav>
    );
};
