import { useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import * as Icon from "./icons";
import { Docs } from "./Docs";
import { Contacts } from "./Contacts";
import { useIsMobile } from "hooks/use-is-mobile";
import { useOpenState } from "@horat1us/react-hooks";
import styles from "./styles.module.scss";
import logo from "../header/logo.svg";
import { ClientLink } from "components/client-link";
import { Social } from "components/social";

export const Layout = () => {
    const isMobile = useIsMobile();
    const [isActive, setActive] = useOpenState();
    const location = useLocation();
    const year = new Date().getFullYear().toString();
    const ref = useRef<HTMLElement | null>(null);
    const toContacts = location.hash === "#contact";

    useEffect(() => {
        const elem = ref.current;
        if (!elem || !toContacts) {
            return;
        }
        const timeout = setTimeout(() => {
            elem.scrollIntoView({
                block: "end",
                inline: "nearest",
                behavior: "smooth",
            });
        }, 300);

        return () => clearTimeout(timeout);
    }, [ref, toContacts]);

    return (
        <footer id="contact" className={styles.footer} ref={ref}>
            <div className="wide-container relative z-10 pt-[60px] mobile:pt-10 mobile:px-4 flex-col">
                <h2 className="text-4xl mobile:text-xl mb-6 mobile:mb-4 text-white font-bold">
                    We’re here for you
                </h2>
                <div className="flex mobile:flex-col w-full justify-between mx-auto relative mb-7">
                    <div className="max-w-[470px] static">
                        <p className="text-xl mobile:text-base">
                            Have questions? Reach out to us anytime via email or
                            any way that’s convenient for you—we’re here to
                            help!
                        </p>
                        <div className="flex mt-6 mobile:mt-4">
                            <ClientLink isCutted />
                            &nbsp;
                            <Social />
                        </div>
                    </div>
                    <div className="flex mobile:mt-7 mobile:flex-col">
                        <Contacts className="contacts_unstyled" />
                        <Docs />
                    </div>
                </div>
            </div>
            <div className={styles.below}>
                <div className="flex mobile:block">
                    <a href="/#home">
                        <img src={logo} alt="Logo" width="66" height="15" />
                    </a>
                    <p
                        className="text-xs ml-2 mobile:ml-0 mobile:mt-2"
                        onDoubleClick={setActive}
                    >
                        {isActive
                            ? `Time: ${process.env.BUILD_TIME}. Build: ${process.env.TRAVIS_BUILD_NUMBER}.`
                            : isMobile
                            ? `© ${year} All rights is reserved`
                            : `© 2022 - ${year}, Kobogo. All rights is reserved`}
                    </p>
                </div>
                <div className={styles.author}>
                    <a
                        href="https://wearesho.com/"
                        target="_blank"
                        rel="noreferrer noopener"
                    >
                        <Icon.Author />
                    </a>
                    <div className={styles.line} />
                    <a
                        href="https://wearesho.com/bobra-cs"
                        target="_blank"
                        rel="noreferrer noopener"
                    >
                        <Icon.Bobra />
                    </a>
                </div>
            </div>
        </footer>
    );
};
