import classNames from "classnames";
import { useComponentVisible } from "hooks/use-component-visible";
import React, { useCallback } from "react";
import { NavLink } from "react-router-dom";
import { DropdownArrow } from "./DropdownArrow";
import { NavItem } from "./Nav";
import styles from "./styles.module.scss";

export const Dropdown: React.FC<{ item: NavItem; navClose?: () => void }> = ({
    item,
    navClose,
}) => {
    const { ref, isComponentVisible, setIsComponentVisible } =
        useComponentVisible();

    const handleClick = useCallback(() => {
        setIsComponentVisible(!isComponentVisible);
        navClose && navClose();
    }, [setIsComponentVisible, isComponentVisible, navClose]);

    return (
        <div className="flex flex-col items-start group">
            <button
                type="button"
                className={classNames(
                    styles.dropdownButton,
                    isComponentVisible ? styles.dropdownButtonActive : ""
                )}
                onClick={() => setIsComponentVisible(!isComponentVisible)}
                ref={ref}
            >
                <span className="mr-2">{item.title}</span>
                <DropdownArrow
                    className={isComponentVisible ? "-scale-y-100" : ""}
                    fillClassName="group-hover:fill-[#1E282D] static"
                />
            </button>
            {isComponentVisible && (
                <ul className={styles.dropdown}>
                    {item.children?.map(({ title, link }) => (
                        <li key={title}>
                            <NavLink to={`${link}`} onClick={handleClick} end>
                                {title}
                            </NavLink>
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
};
