import classNames from "classnames";
import React from "react";
import { GooglePlay } from "./GooglePlay";
import { GooglePlayCutted } from "./GooglePlayCutted";
import "./styles.scss";

export const ClientLink: React.FC<{
    isCutted?: boolean;
    isLightTheme?: boolean;
}> = ({ isCutted, isLightTheme }) => {
    return (
        <a
            href="https://play.google.com/store/apps/details?id=ng.kobogo.mobile"
            className={classNames({
                "inline-block mobile:my-0": !isCutted,
                "light-theme": isLightTheme,
            })}
        >
            {isCutted ? <GooglePlayCutted /> : <GooglePlay />}
        </a>
    );
};
