import { memo, useEffect } from "react";
import classNames from "classnames";
import { useOpenState } from "@horat1us/react-hooks";
import { PopUp } from "./pop-up";
import { useIsMobile } from "hooks/use-is-mobile";
import "./styles.scss";
import { Image } from "./image";

export const Bot = memo(() => {
    const [active, changeActive] = useOpenState(true);
    const isMobile = useIsMobile();

    useEffect(() => {
        const listener = () => changeActive();
        document.addEventListener("DOMContentLoaded", listener);
        return () => document.removeEventListener("DOMContentLoaded", listener);
    }, [changeActive]);

    useEffect(() => {
        if (!active) {
            return;
        }
        const id = setTimeout(() => changeActive(), isMobile ? 1500 : 15000);
        return () => clearTimeout(id);
    }, [active, changeActive, isMobile]);

    return (
        <div
            onClick={changeActive}
            className={classNames(
                `animation-container turned`,
                active && "click-animation"
            )}
        >
            {!isMobile && active && <PopUp />}
            <Image />
        </div>
    );
});

Bot.displayName = "Bot";
